<template>
	<view class="chat-container">
		<scroll-view class="chat-content container-list" scroll-y  :scroll-with-animation="true" :scroll-top="scrollTop">
			<view id="chat-box">
				<view v-for="(message, index) in messages" :key="index" class="message p-5">
					<view v-if="message.sender === 'me'" class="my-message">
						<view class="message-text">{{ message.text }}</view>
					</view>
					<view v-else class="other-message">
						<view class="message-sender">{{ message.sender }}</view>
						<view class="message-text">{{ message.text }}</view>
					</view>
				</view>
			</view>
		</scroll-view>
		<view class="chat-input">
			<input v-model="inputText" type="text" placeholder="请输入信息" @keyup.enter="sendMessage" />
			<u-button @click="sendMessage">Send</u-button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				inputText: '',
				scrollTop: 0,
				messages: [{
						sender: 'other',
						text: 'Hi, how are you?'
					},
					{
						sender: 'me',
						text: 'I am good, thanks. How about you?'
					},
					{
						sender: 'other',
						text: 'I am fine too. What are you up to today?'
					},
					{
						sender: 'me',
						text: 'Not much, just working on some projects. How about you?'
					},
				]
			}
		},
		mounted() {
			this.safariHacks();
		},
		methods: {
			sendMessage() {
				if (!this.inputText) return
				this.messages.push({
					sender: 'me',
					text: this.inputText
				})
				this.inputText = ''
				this.scrollToView()
			},
			scrollToView(){
				let that = this
				
				uni.createSelectorQuery().select('#chat-box').boundingClientRect(function(rect) {
					that.scrollTop = rect.height
				}).exec()
			},
			safariHacks() {
				let convertStyle = function(){
					setTimeout(function(){
						let bottomHeight = document.querySelector(".chat-input").offsetHeight + "px"
						, windowsVH = window.innerHeight + "px";
						document.querySelector('.container-list').style.setProperty("height", "calc("+ windowsVH + " - " + bottomHeight + ")", "important");
					},1000)
				} 
				window.addEventListener('resize', function() {
					convertStyle()
				});
				convertStyle()
			}
		}
	}
</script>

<style lang="less" scoped>
	.chat-container {
		height: 100%;
	}

	.chat-content {
		flex: 1;
		padding-left: 20px;
		padding-right: 20px;
	}

	.my-message {
		align-self: flex-end;
		background-color: #4CAF50;
		color: white;
		max-width: 70%;
		padding: 10px;
		border-radius: 10px;
	}

	.other-message {
		align-self: flex-start;
		background-color: #f2f2f2;
		color: black;
		max-width: 70%;
		padding: 10px;
		border-radius: 10px;
	}

	.message-sender {
		font-weight: bold;
		margin-bottom: 5px;
	}

	.chat-input {
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100vw;
		
		display: flex;
		align-items: center;
		padding: 5px 10px;
		background-color: #f2f2f2;
	}

	.chat-input input {
		flex: 1;
		padding: 10px;
		border-radius: 5px;
		border: none;
		outline: none;
	}

	.chat-input button {
		margin-left: 10px;
		padding: 10px;
		border-radius: 5px;
		border: none;
		outline: none;
		background-color: #4CAF50;
		color: white;
	}
</style>